<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process155"></a>

<p><br></p>
<p><span class="rvts17">数小鸡（</span><span class="rvts50">Counting Your Chickens</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:445,Y:93</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：大母鸡麦吉（</span><span class="rvts20">Big Marge</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">找回丢失的鸡蛋</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1117.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">可以在浮木城东城门桥头路东边找到一个鸡圈。鸡圈里面有</span><span class="rvts20">3</span><span class="rvts21">只鸡，使用</span><span class="rvts20"></span><span class="rvts21">宠物伙伴（</span><span class="rvts20">Pet Pal</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">天赋与其中的大母鸡麦吉谈话，它会告诉你它丢了很多鸡蛋。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1118.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">顺着麦吉提示的方向，可以找到一处被虚空怪占领的海滩。打败虚空怪，却发现所有的鸡蛋都损坏严重，只剩下一个鸡蛋。</span>
<br><span class="rvts21">把这个鸡蛋带回鸡圈，作为奖励，麦吉将会告诉你鸡圈后面有宝藏。之后，麦吉会开始孵化这支蛋。此时需要去做其它事情，到一定的时间后，再来观察鸡舍。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1119.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">在隔了一段很长的时间后，来到鸡舍，却发现所有的鸡都被咬死了，除了被孵化出来的皮皮（</span><span class="rvts20">Peeper</span><span class="rvts21">）。和皮皮对话，它便会尾随着你。</span>
<br><span class="rvts21">使用</span><span class="rvts20"></span><span class="rvts21">源力视野</span><span class="rvts20"></span><span class="rvts21">技能，查看鸡舍，会找到大母鸡麦吉的灵魂，它希望带上皮皮，找到它的父亲来了结此事。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1120.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">最终来到魔法大公鸡的位置（坐标，</span><span class="rvts20">X:439,Y:403</span><span class="rvts21">），询问公鸡关于皮皮的问题。然而大公鸡却要求杀掉皮皮，因为皮皮已经被虚空兽给污染失去了心智。</span>
<br><span class="rvts21">如果选择杀掉皮皮，那么将获得魔法大公鸡的奖励和一个额外的魔法宝箱。如果拒绝，</span><span class="rvts20">3</span><span class="rvts21">只虚空虫子将被召唤来触发战斗。杀死</span><span class="rvts20">3</span><span class="rvts21">只虫子奖励</span><span class="rvts20">3</span><span class="rvts21">个源力球，但不会获得魔法公鸡的额外奖励。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



